import React from 'react';

const Card = (props) => {
    const { name, email } = props;
    return (

        <div className='bg-light-green dib ma2 br3 pa3 grow bw2 mt3 shadow-5'>
        <img alt="robots" src= {`https://robohash.org/${props.id}?200x200`} />
        <div>
            <h2>{name}</h2>
            <p>{email}</p>
        </div>    
        </div>
    );
}

export default Card;